*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5% 
}

body {
  background-color: #2a9d8f;
}

.main-wrapper {
  width: 70rem;
  height: 70rem;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-row-gap: 15px;
  grid-column-gap: 15px;
  
}

.background-wrapper {
  width: 70rem;
  height: 70rem;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  background-color: #2ec4b6;
  z-index: -1;

}

.item-card {
  background-color: #2a9d8f; 
  transform: 1s all;
  position: relative;
  cursor: pointer;
}


.o-icon {
  font-size: 30rem;
  color: #0066ff;
  position: absolute;
  top: -4rem;
  left: -4rem;
  animation-name: fadeIn;
  animation-duration: .5s;
}

.x-icon {
   font-size: 23rem;
  color :#e5383b;
  font-weight: 700;
  position: absolute;
  top: rem;
  left: -.5rem;
  animation-name: fadeIn;
  animation-duration: .5s;
}

.pointer {
  pointer-events: none;
}

.show-player-box {
  width: 80rem;
  height: 5rem;
  margin: auto;
  margin-top: 3rem;
  display: flex;
  justify-content: space-around;
 

}

.player-box {
  width: 25rem;
  background-color: #fff;
  border-radius: 10px;
   border: .5px solid rgb(151, 151, 151);
  transition: 1s all;
  font-size: 3rem;
  padding-left: 2rem;
  position: relative;
}

.score {
  position: absolute;
  top: 0rem;
  right: 2rem;
  font-weight: 550;
}

.active-o {
  box-shadow: 2px 5px 13px 2px #0000009e;
  border-bottom: 5px solid #0066ff;
}

.active-x {
  box-shadow: 2px 5px 13px 2px #0000009e;
  border-bottom: 5px solid #e5383b;
}

.gameEnd-wrapper {
  position: absolute;
  width: 100%;
  height: 100vh;
 
  top : 0;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;

}

.winner-o-icon {
  color: #0066ff;
  font-size: 90rem;
  position: absolute;
  top: -15rem;
  animation-name: fadeIn;
  animation-duration: 2s;
}

.winner-x-icon {
  color: #e5383b;
  font-size: 70rem;
  position: absolute;
  top: -5rem;
  animation-name: fadeIn;
  animation-duration: 2s;
}

.win {
  font-size: 10rem;
  font-weight: 700;
  margin-top: 35rem;
  animation-name: win;
  animation-duration: 1s;
}

.animation-box {
  width: 300px;
  height: 300px;
  position: absolute;
  overflow: hidden;
}

.winner-draw {
  font-size: 20rem;
  font-weight: 700;
  animation-name: win;
  animation-duration: 1s;
  margin-bottom: 3rem;
}

@keyframes win {
  0% {
    transform : translateY(0px); /* 애니메이션이 0%만큼 동작시 */
  }
  20% {
    transform : translateY(-100px); /* 애니메이션이 50%만큼 동작시 */
  }
  

  

  100% {
    transform : translateY(0px); /* 애니메이션이 100%만큼 동작시 */
  }
}


.restart-btn {
  position: absolute;
  font-size: 5rem;
  cursor: pointer;
  top: 75rem;
  border-radius: 10px;
  padding: 1rem 3rem;
  border: none;
  animation-name: fadeIn;
  animation-duration: 2s;
  
}

.restart-btn:hover {
  color: #fff;
  background-color: #000;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }

}

.hide {
  display: none;
}

.o-PlayerIcon {
  font-size: 5rem;
  position: absolute;
  left: .5rem;
  top: -.3rem;
  color:#0066ff
}

.x-PlayerIcon {
  font-size: 3.5rem;
  color:#e5383b;
  position: absolute;
  top: .5rem;
  left: 1.2rem;
}